<script>
export default {
  name: 'CalendarDemo',
  data() {
    return {
      date: new Date().getTime(),
      marks: [
        {
          title: '事件录像',
          timestamp: new Date().getTime() - 24 * 60 * 60 * 1000 * 3,
          type: 'event'
        },
        {
          title: '定时录像',
          timestamp: new Date().getTime() + 24 * 60 * 60 * 1000 * 3,
          type: 'time'
        },
        {
          title: '定时录像',
          timestamp: new Date().getTime() - 24 * 60 * 60 * 1000 * 2,
          type: 'time'
        },
        {
          title: '定时录像',
          timestamp: new Date().getTime() + 24 * 60 * 60 * 1000 * 2,
          type: 'time'
        },
        {
          title: '事件录像',
          timestamp: new Date().getTime() + 24 * 60 * 60 * 1000 * 3,
          type: 'event'
        },
        {
          title: '定时录像',
          timestamp: new Date().getTime() + 24 * 60 * 60 * 1000 * 15,
          type: 'time'
        },
        {
          title: '事件录像',
          timestamp: new Date().getTime() + 24 * 60 * 60 * 1000 * 13,
          type: 'event'
        }
      ],
      theme: {
        mainColor: '#212121',
        textColor: '#0D7377',
        darkTextColor: '#323232',
        highlightColor: '#14FFEC',
        eventColor: '#da8204',
        timeColor: '#32e184'
      }
    }
  }
}
</script>


